
.navbar {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 1rem 2rem;
    background-color: #000;
    color: white;
  }
  
  .brand {
    font-size: 1.5rem;
  }
  .brand:hover{
    font-size: 1.6rem;
  }
  .get-users-button {
    background-color: #ddd20a;
    border-radius: 32px;
    color: black;
    border: 3px solid rgb(202, 82, 7);
    padding: 0.5rem 1rem;
    cursor: pointer;
  }
  .get-users-button:hover{
    font-weight: 700;
  }
  
  .user-card {
    border: 3px solid #110e0e;
    border-radius: 18px 50px;
    padding: 1rem;
    margin: 1rem;
    text-align: center;
    background-color: rgb(139, 137, 137);
  }
  .user-card:hover{
    background-color: rgb(228, 109, 39);
  }
  
  .user-card img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-bottom: 0.5rem;
  }
  
  .user-grid {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    flex-wrap: wrap;
    justify-content: center;
    margin: 35px 50px;
  }

